<template>
  <div id="login">
      <div class="loginbox">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm">
            <el-form-item  prop="username" >
                <el-input v-model="ruleForm.username" placeholder="请输入账号" type="text" prefix-icon='el-icon-user-solid' ></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-input type="password" v-model="ruleForm.password" placeholder="请输入密码"
                prefix-icon='el-icon-lock'></el-input>
            </el-form-item>
            <el-form-item>
                <el-button  @click="submitForm('ruleForm')">提交</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
      </div>     
  </div>
</template>

<script>
 export default {
    data() {
      return {
        ruleForm: {
          username: 'admin',
          password: '123456'
        },
        rules: {
         username:[
            {required:true,message:"请输入账号",trigger:'blur'},
            {min:4,max:12,message:"长度在4到12之间",trigger:"blur"}
         ],
         password:[
            {required:true,message:"请输入密码",trigger:'blur'},
            {min:4,max:16,message:"长度在4到16之间",trigger:"blur"},
         ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
            if(valid){
                this.$axios.get('http://localhost:3000/users')
                .then(res=>{
                  let {data}=res;
                  let value=data.find(item=>{
                    return item.username==this.ruleForm.username&&item.password==this.ruleForm.password;
                  })
                  if(value){
                      window.sessionStorage.setItem('userinfo',this.ruleForm.username)
                      this.$message.success('登录成功')
                      this.$router.push('/home')
                  }else{
                    this.$router.push('/register')
                    return this.$message.error('登录失败')
                  }
                })
            }            
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang='less' scoped>
#login{
    height: 100%;
    background-color: #ccc;
}
.loginbox{
    width: 30%;
    background-color: #fff;
    padding: 30px 40px;
    border-radius: 10px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

</style>