<template>
  <div id="register">
    <div class="registerbox">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm">
            <el-form-item  prop="username" >
                <el-input v-model="ruleForm.username" placeholder="请输入要注册的账号" type="text" prefix-icon='el-icon-user-solid' ></el-input>
            </el-form-item>
            <el-form-item  prop="password">
                <el-input type="password" v-model="ruleForm.password" placeholder="请输入密码"
                prefix-icon='el-icon-lock'></el-input>
            </el-form-item>
            <el-form-item>
                <el-button  @click="submitForm('ruleForm')">提交</el-button>
                <el-button @click="resetForm('ruleForm')">重置</el-button>
            </el-form-item>
        </el-form>
      </div>   
  </div>
</template>

<script>
 export default {
    data() {
      return {
        ruleForm: {
          username: '',
          password: ''
        },
        rules: {
         username:[
            {required:true,message:"请输入账号",trigger:'blur'},
            {min:4,max:12,message:"长度在4到12之间",trigger:"blur"}
         ],
         password:[
            {required:true,message:"请输入密码",trigger:'blur'},
            {min:4,max:16,message:"长度在4到16之间",trigger:"blur"},
         ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if(valid){
            this.$axios.post('http://localhost:3000/users',this.$qs.stringify({ 'username': this.ruleForm.username,
            password: this.ruleForm.password }))
            .then(res => {
              if(res.data!=""){
                this.$message.error(res.data)
              }else{
                this.$message.success('注册成功')
                this.$router.push('./login')
              }
            });
          }            
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style lang='less' scoped>
#register{
    height: 100%;
    background-color: #ccc;
}
.registerbox{
    width: 30%;
    background-color: #fff;
    padding: 30px 40px;
    border-radius: 10px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}
</style>