<template>
  <div id="">
     <el-card>
        <!-- 为ECharts准备一个具备大小（宽高）的Dom -->
        <div class="box">
          <div id="main" style="width: 500px;height:400px;"></div>
          <div id="main1" style="width: 500px;height:400px;"></div>
        </div>
        <div class="box">
           <div id="main2" style="width: 500px;height:400px;"></div>
          <div id="main3" style="width: 500px;height:400px;"></div>
        </div>
           
     </el-card>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  name: '',
  data() { 
    return {

    }
  },
  mounted() {
    // 基于准备好的dom，初始化echarts实例
    var myChart = echarts.init(document.getElementById('main'));

    var myChart1 = echarts.init(document.getElementById('main1'));

    var myChart3 = echarts.init(document.getElementById('main3'));

    var option = {
            title: {
                text: '花礼网销售图表'
            },
            tooltip: {},
            legend: {
                data:['销量']
            },
            xAxis: {
                data: ["送恋人","送长辈","永生花","蛋糕","礼品","巧克力"]
            },
            yAxis: {},
            series: [{
                name: '销量',
                type: 'bar',
                data: [15, 12.5, 30, 10, 10, 20]
            }]
        };
        var option1 = {
            backgroundColor: '#2c343c',
            visualMap: {
                show: false,
                min: 80,
                max: 600,
                inRange: {
                    colorLightness: [0, 1]
                }
            },
            series : [
                {
                    name: '访问来源',
                    type: 'pie',
                    radius: '55%',
                    data:[
                        {value:235, name:'蛋糕广告'},
                        {value:274, name:'巧克力广告'},
                        {value:310, name:'礼品营销'},
                        {value:335, name:'浏览器访问'},
                        {value:400, name:'搜索引擎'}
                    ],
                    roseType: 'angle',
                    label: {
                        normal: {
                            textStyle: {
                                color: 'rgba(255, 255, 255, 0.3)'
                            }
                        }
                    },
                    labelLine: {
                        normal: {
                            lineStyle: {
                                color: 'rgba(255, 255, 255, 0.3)'
                            }
                        }
                    },
                    itemStyle: {
                        normal: {
                            color: '#c23531',
                            shadowBlur: 200,
                            shadowColor: 'rgba(0, 0, 0, 0.5)'
                        }
                    }
                }
            ]
        };
        echarts.init(document.getElementById('main2')).setOption({
            title: {text: '花礼网折线图'},
            tooltip: {},
            xAxis: {},
            yAxis: {},
            series: [{
                type: 'line',
                smooth: true,
                data: [[12, 5], [24, 20], [36, 36], [48, 10], [60, 10], [72, 20]]
            }]
        });
        var option2 = {
          series: {
              type: 'sunburst',
              data: [{
                  name: '花',
                  value: 10,
                  children: [{
                      value: 3,
                      name: '物品'
                  }, {
                      value: 5,
                      name: '广告'
                  }]
              }, {
                  name: '用户',
                  children: [{
                      name: '销售',
                      value: 4
                  }, {
                      name: '好评',
                      value: 2
                  }]
              }, {
                  name: '推广',
                  value: 3
              }]
          }
      }
      myChart.setOption(option);
      myChart1.setOption(option1);
      myChart3.setOption(option2);

  },
 }
</script>

<style lang='less' scoped>
  .box{
    display: flex;
    justify-content: space-around;
  }
</style>