import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Login from "../views/Login.vue";
import Register from "../views/Register.vue";
import First from "../views/First.vue";
import UserOne from "../views/second/UserOne.vue";
import OrderOne from "../views/second/OrderOne.vue";
import ProductOne from "../views/second/ProductOne.vue";
import PersonOne from "../views/second/PersonOne.vue";
import AddProduct from "../views/detail/AddProduct.vue";
import UpdateProduct from "../views/detail/UpdateProduct.vue";

Vue.use(VueRouter);

const routes = [
  {
    path:"/login",
    name:"Login",
    component:Login
  },
  {
    path:"/register",
    name:"Register",
    component:Register
  },
  {
    path:"/",
    name:"index",
    component:Home,
    redirect:"/first",
  },
  {
    path: "/home",
    name: "Home",
    component: Home,
    redirect:"/first",
    children:[
      {
        path: "/first",
        name: "First",
        component: First,
      },
      {
        path: "/userOne",
        name: "UserOne",
        component: UserOne,
      },
      {
        path: "/orderOne",
        name: "OrderOne",
        component: OrderOne,
      },
      {
        path: "/productOne",
        name: "ProductOne",
        component: ProductOne,
      },
      {
        path: "/personOne",
        name: "PersonOne",
        component: PersonOne,
      },
      {
        path: "/addProduct",
        name: "AddProduct",
        component: AddProduct,
      },
      {
        path: "/updateProduct",
        name: "UpdateProduct",
        component: UpdateProduct,
      }
    ]
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to,from,next)=>{
  if(to.path=='/login' || to.path=='/register'){
    return next()
  }
  let userinfo=window.sessionStorage.getItem('userinfo')
  if(!userinfo){
    return next('/login')
  }
    next() 
})

export default router;
