import {
    Form,
    FormItem,
    Input,
    Button,
    Message,
    MessageBox,
    container,
    header,
    aside,
    main,
    menu,
    submenu,
    menuItem,
    Breadcrumb,
    BreadcrumbItem,
    Card,
    Col,
    Table,
    TableColumn,
    Pagination,
    Dialog,
    Upload
} from 'element-ui'

const element={
    install:function(Vue){
        Vue.use(Form)
        Vue.use(FormItem)
        Vue.use(Input)
        Vue.use(Button)
        Vue.use(container)
        Vue.use(header)
        Vue.use(aside)
        Vue.use(main)
        Vue.use(menu)
        Vue.use(submenu)
        Vue.use(menuItem)
        Vue.use(Breadcrumb)
        Vue.use(BreadcrumbItem)
        Vue.use(Card)
        Vue.use(Col)
        Vue.use(Table)
        Vue.use(TableColumn)
        Vue.use(Pagination)
        Vue.use(Dialog)
        Vue.use(Upload)
        Vue.prototype.$message = Message
        Vue.prototype.$msgbox = MessageBox
    }
}
export default element