<template>
  <div id>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>用户管理</el-breadcrumb-item>
      <el-breadcrumb-item>用户列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-col :span="4">
        <el-button type="primary"  @click="addUserData = true">添加用户</el-button>
      </el-col>
      <el-table :data="list" stripe border>
        <el-table-column label="序号" prop="id"></el-table-column>
        <el-table-column label="姓名" prop="username"></el-table-column>
        <el-table-column label="邮箱" prop="email"></el-table-column>
        <el-table-column label="电话" prop="phonenumber"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="edit(scope.$index)"></el-button>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="del(scope.$index)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- 模态框一 -->
    <el-dialog title="添加用户" :visible.sync="addUserData" width="50%">
      <el-form :model="ruleForm" :rules="rules" ref="ruleFormRef" label-width="70px">
        <el-form-item prop="username" label="姓名">
          <el-input v-model="ruleForm.username" placeholder="请输入账号" type='text'></el-input>
        </el-form-item>
        <el-form-item prop="email" label="邮箱">
          <el-input v-model="ruleForm.email" placeholder="请输入邮箱"  type='email'></el-input>
        </el-form-item>
        <el-form-item prop="phonenumber" label="电话">
          <el-input v-model="ruleForm.phonenumber" placeholder="请输入电话号码" type='text'></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addUserData = false">取 消</el-button>
        <el-button type="primary" @click="addUser()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 模态框二-->
    <el-dialog title="修改用户" :visible.sync="updateUserData" width="50%">
      <el-form :model="updateList" :rules="rules" ref="updateListRef" label-width="70px">
        <el-form-item prop="username" label="姓名">
          <el-input v-model="updateList.username" type='text'></el-input>
        </el-form-item>
        <el-form-item prop="email" label="邮箱">
          <el-input v-model="updateList.email" placeholder="请输入邮箱" type='email'></el-input>
        </el-form-item>
        <el-form-item prop="phonenumber" label="电话">
          <el-input v-model="updateList.phonenumber" placeholder="请输入电话号码" type='text'></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="updateUserData = false">取 消</el-button>
        <el-button type="primary" @click="editData()">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 模态框三 -->

    <el-dialog title="删除用户" :visible.sync="delUserData" width="40%" >
      <span>确定删除此账户</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delUserData = false">取 消</el-button>
        <el-button type="primary" @click="delData()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      list: [],
      updateList: {},
      addUserData: false,
      updateUserData: false,
      delUserData:false,
      tabeleIndex: "",
      ruleForm: {
        id:"",
        username: "",
        email: "",
        phonenumber: ""
      },
      rules: {
        id:[
          { required: true, message: "请输入序号", trigger: "blur" },
          { min: 1, max: 12, message: "长度在1到12之间", trigger: "change" }
        ],
        username: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 4, max: 12, message: "长度在4到12之间", trigger: "change" }
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" },
          { min: 4, max: 60, message: "长度在4到60之间", trigger: "change" }
        ],
        phonenumber: [
          { required: true, message: "请输入电话号码", trigger: "blur" },
          { min: 4, max: 16, message: "长度在4到16之间", trigger: "change" }
        ]
      }
    };
  },
  created() {
    this.getUser();
  },
  methods: {
    getUser() {
      this.$axios.get("http://localhost:3000/person").then(res => {
        let { data } = res;
        this.list = data;
      });
    },
    addUser() {
      this.$refs.ruleFormRef.validate(vaild => {
        if (vaild) {
          let id = this.list.length + 1; //获取数组的长度并加1 使得有其id值
          this.$axios
            .post(
              "http://localhost:3000/person/insertUsers",
              this.$qs.stringify({
                id: id,
                username: this.ruleForm.username,
                email: this.ruleForm.email,
                phonenumber: this.ruleForm.phonenumber
              })
            )
            .then(res => {
              if (res.data != "") {
                this.$message.error(res.data);
              } else {
                this.$message.success("添加个人账号成功");
              }
              this.addUserData = false;
              this.getUser();
              this.ruleForm={
              }
            });
        }
      });
    },
    edit(index) {
      this.updateUserData = true;
      this.tabeleIndex = index + 1;
      let updateData = {
        username: this.list[index].username,
        email: this.list[index].email,
        phonenumber: this.list[index].phonenumber
      };
      this.updateList = updateData;
    },
    editData() {
      this.$refs.updateListRef.validate(vaild => {
        if (vaild) {
          this.$axios
          .post(
            "http://localhost:3000/person/updateUsers",
            this.$qs.stringify({
              id: this.tabeleIndex,
              username: this.updateList.username,
              email: this.updateList.email,
              phonenumber: this.updateList.phonenumber
            })
          )
          .then(res => {
            if (res.data == 0) {
              this.$message.error("用户名已存在");
            } else {
              this.$message.success(res.data);
            }
            this.updateUserData = false;
            this.getUser();
          });
        }
      });
    },
    del(index) {
      this.delUserData = true;
      this.tabeleIndex = index + 1;
    },
    delData(){
      this.$axios
      .post(
        "http://localhost:3000/person/delUsers",
        this.$qs.stringify({
          id: this.tabeleIndex
        })
      )
      .then(res => {
        this.$message.success(res.data);
        this.delUserData = false;
        this.getUser();
      });
    }
  }
};
</script>

<style lang='less' scoped>
.el-breadcrumb {
  margin-bottom: 15px;
  font-size: 15px;
}
.el-card {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  .el-table {
    margin-top: 60px;
    font-size: 16px;
  }
}
</style>