<template>
  <div id="">
      <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item >订单管理</el-breadcrumb-item>
      <el-breadcrumb-item>订单详情</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
      <el-table :data="list.slice((pagenum-1)*pagesize,pagenum*pagesize)" stripe border>
        <el-table-column label='序号' prop='id'></el-table-column>
        <el-table-column label='名字' prop='name'></el-table-column>
        <el-table-column label='价格' prop='newPrice'></el-table-column>
        <el-table-column label='购买总数' prop='num'></el-table-column>
        <el-table-column label='数量' prop='thisNum'></el-table-column>
        <el-table-column label='操作'>
          <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size='mini' @click='updateOrder(scope.$index)'></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>

    </el-card>

    <el-dialog title="修改订单" :visible.sync="updateOrderData" width="50%">
      <el-form :model="updateList"  label-width="70px">
        <el-form-item prop="name" label="名字">
          <el-input v-model="updateList.name" type="text"></el-input>
        </el-form-item>
        <el-form-item prop="newPrice" label="价格">
          <el-input v-model="updateList.newPrice" type="text"></el-input>
        </el-form-item>
        <el-form-item prop="num" label="购买总数">
          <el-input v-model="updateList.num" type="text"></el-input>
        </el-form-item>
        <el-form-item prop="thisNum" label="数量">
          <el-input v-model="updateList.thisNum" type="text"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="updateOrderData = false">取 消</el-button>
        <el-button type="primary" @click="editData()">确 定</el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  name: '',
  data() { 
    return {
       list: [],
       updateOrderData: false,
       updateList: {},
       tabeleIndex: "",
       total:0,
       pagenum:1,
       pagesize:3
    }
  },
  created() {
     this.getData();
  },
  methods: {
    getData(){
      this.$axios.get('http://localhost:3000/order')
      .then(res=>{
          let {data}=res;
          this.list=data
          this.total=this.list.length
        })
     },
      handleSizeChange(newSize){
      this.pagesize=newSize
      this.getUser()
    },

    handleCurrentChange(newsPage){
      this.pagenum=newsPage
      this.getUser()
    },
     updateOrder(index){
       this.updateOrderData = true;
       this.tabeleIndex = index + 1;
       let updateData = {
        name: this.list[index].name,
        newPrice: this.list[index].newPrice,
        num: this.list[index].num,
        thisNum: this.list[index].thisNum
       };
      this.updateList = updateData;
     },
     editData() {
      this.$axios
      .post(
        "http://localhost:3000/order/updateOrder",
        this.$qs.stringify({
          id: this.tabeleIndex,
          name: this.updateList.name,
          newPrice: this.updateList.newPrice,
          num: this.updateList.num,
          thisNum: this.updateList.thisNum
        })
      )
      .then(res => {
        if (res.data == 0) {
          this.$message.error("用户名已存在");
        } else {
          this.$message.success(res.data);
        }
        this.updateOrderData = false;
        this.getData();
      });
    }, 
   },
 }
</script>

<style lang='less' scoped>
.el-breadcrumb{
  margin-bottom: 15px;
  font-size: 15px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  a{
    text-decoration: none;
  }
  .el-table{
    font-size: 16px;
  }
}
div{
  text-align: center;
}
.el-button{
  margin-top: 20px;
}
</style>