<template>
  <div id>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
    </el-breadcrumb>
     <el-card >
      <el-col :span="4">
        <a href='/addProduct'><el-button type='primary'>添加商品主页</el-button></a>
      </el-col> 
      <el-table :data="list.slice((pagenum-1)*pagesize,pagenum*pagesize)" stripe border>
        <el-table-column label='序号' prop='id'></el-table-column>
        <el-table-column label='名字' prop='name'></el-table-column>
        <el-table-column label='描述' prop='Science'></el-table-column>
        <el-table-column label='价格' prop='newPrice'></el-table-column>
        <el-table-column label='图片' prop='img'>
          <template slot-scope="scope">            
            <img :src="'http://localhost:3000/'+scope.row.img"  min-width="70" height="70" />
          </template>
        </el-table-column>
        <el-table-column label='操作'>
          <template slot-scope="scope">
              <el-button type="primary" icon="el-icon-edit" size='mini' @click='updateProduct(scope.$index)'></el-button>
              <el-button type="danger" icon="el-icon-delete" size='mini'  @click='deleteProduct(scope.$index)'></el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagenum"
        :page-sizes="[1, 3, 5, 10]"
        :page-size="pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      list:[],
      total:0,
      pagenum:1,
      pagesize:3
    };
  },
  created() {
    this.getUser()
  },
  methods: {
    getUser(){
      this.$axios.get('http://localhost:3000/shop')
      .then(res=>{
        let {data}=res;
        this.list=data
        this.total=this.list.length
      })
    },
    handleSizeChange(newSize){
      this.pagesize=newSize
      this.getUser()
    },

    handleCurrentChange(newsPage){
      this.pagenum=newsPage
      this.getUser()
    },
    updateProduct(index){
      let id=index+1;
      this.$router.push({
        path: `/updateProduct?id=${id}`,
      })
    },
    deleteProduct(index){
      let id=index+1;
      this.$axios.post('http://localhost:3000/shop/deleteProduct',{id})
      .then(res=>{
         this.$message.success('删除成功')
          this.getUser()
      });
    }
  },
};
</script>

<style lang='less' scoped>
.el-breadcrumb{
  margin-bottom: 15px;
  font-size: 15px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  a{
    text-decoration: none;
  }
  .el-table{
    margin-top: 60px;
    font-size: 16px;
  }
}
.el-pagination{
  text-align: center;
}
</style>