<template>
  <div id="">
      <!-- 面包屑导航 -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ path: '/productOne' }">商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品添加</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card>
        <el-form :model="ruleForm"  label-width="70px" enctype="multipart/form-data">
        <el-form-item prop="name" label="名字">
          <el-input v-model="ruleForm.name"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="Science" label="描述">
          <el-input v-model="ruleForm.Science"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="img" label="图片">
        <el-upload
          action="#"
          :on-remove="removeFile"
          :on-change="handleChanged"
          :data='data.fileList1'
          :auto-upload="false"
          >
          <el-button class="card-foot-btn" icon="upload2">选取文件</el-button>
        </el-upload>
        </el-form-item>
        <el-form-item prop="ScienceAll" label="详情描述">
          <el-input v-model="ruleForm.ScienceAll"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="flowerLanguage" label="花语">
          <el-input v-model="ruleForm.flowerLanguage"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="promo" label="标签">
          <el-input v-model="ruleForm.promo"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="newPrice" label="新价格">
          <el-input v-model="ruleForm.newPrice"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="oldPrice" label="旧价格">
          <el-input v-model="ruleForm.oldPrice"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="num" label="售出数量">
          <el-input v-model="ruleForm.num"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="thisNum" label="商品数量">
          <el-input v-model="ruleForm.thisNum"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="reduceFlag" label="导航判断">
          <el-input v-model="ruleForm.reduceFlag"  type="text"></el-input>
        </el-form-item>
        <el-form-item prop="dataitem" label="商品类型">
          <el-input v-model="ruleForm.dataitem"  type="text"></el-input>
        </el-form-item>
        <el-form-item  label="轮播图片">
        <el-upload
          action="#"
          :show-file-list="true"
          :on-remove="removeFile"
          :multiple="true"
          :data='data.fileList'
          :on-change="handleChange"
          :auto-upload="false"
        >
        <el-button class="card-foot-btn" icon="upload2">选取文件</el-button>
      </el-upload>
        </el-form-item>
      <div >
        <el-button @click="uploadFile" type="primary">上传</el-button>
        <el-button @click="uploadVisiable = false, fileList = []">取消</el-button>
      </div>
      </el-form>
    </el-card>
  </div>
</template>

<script>
export default {
  name: '',
  data() { 
    return {
      ruleForm: {
          name: "",
          Science: "",
          ScienceAll:'',
          flowerLanguage:'',
          promo:'',
          newPrice:'',
          oldPrice:'',
          num:'',
          thisNum:'',
          reduceFlag:'',
          dataitem:'',
          img:''
      },
      uploadVisiable: true,
      data:[
        {fileList:[]},
        {fileList1:[]}
      ]

    }
  },
  created() {
   
  },
  methods: {
      handleChange (fileList) {
        this.data[0].fileList.push(fileList)
      },
      handleChanged (fileList) {
        // console.log(this.data[1].fileList1)
        this.data[1].fileList1.push(fileList)
      },
      // 移除文件
      removeFile (file) {
          // 移除文件时，要重新给fileList赋值
           let arr = []
            for (let i = 0; i < this.data[0].fileList.length; i++) {
                if (this.data[0].fileList[i].uid !== file.uid) {
                arr.push(this.data[0].fileList[i])
                }
            }
            this.data[0].fileList = arr
      },
      // 手动文件上传
      uploadFile () {
        if (this.data[0].fileList.length === 0) {
          this.$message.warning('请选取文件')
          return
        }
        if (this.data[1].fileList1.length === 0) {
          this.$message.warning('请选取文件')
          return
        }
        const formData = new FormData()
        // 因为要传一个文件数组过去，所以要循环append
        this.data[0].fileList.forEach((file) => {
            formData.append('file', file.raw)
        })
        this.data[1].fileList1.forEach((file) => {
            formData.append('img', file.raw)
        })
          let config = {
          headers: { 'Content-Type': 'multipart/form-data' }
        }; 
        formData.append('name', this.ruleForm.name) 
        formData.append('Science', this.ruleForm.Science) 
        formData.append('ScienceAll', this.ruleForm.ScienceAll) 
        formData.append('flowerLanguage', this.ruleForm.flowerLanguage) 
        formData.append('promo', this.ruleForm.promo) 
        formData.append('newPrice', this.ruleForm.newPrice) 
        formData.append('oldPrice', this.ruleForm.oldPrice) 
        formData.append('num', this.ruleForm.num) 
        formData.append('thisNum', this.ruleForm.thisNum) 
        formData.append('reduceFlag', this.ruleForm.reduceFlag) 
        formData.append('dataitem', this.ruleForm.dataitem) 
        // formData.append('org_id', 'org_id') // 自定义参数
        this.$axios.post('http://localhost:3000/shop/addProduct',formData,config).then(res => {               
          this.data[0].fileList = [];
          this.data[1].fileList1 = [];
          this.$router.push('/productOne')
        })
      },
  },
 }
</script>

<style lang='less' scoped>
.el-breadcrumb{
  margin-bottom: 15px;
  font-size: 15px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  a{
    text-decoration: none;
  }
  .el-table{
    margin-top: 60px;
    font-size: 16px;
  }
}
div{
  text-align: center;
}
.el-button{
  margin-top: 20px;
}
</style>